:root {
    --max-width: 1424px;

    --bg-scroll-bar: linear-gradient(180deg, rgba(0, 136, 174, 0.683), rgba(0, 136, 174, 0.822), rgba(0, 136, 174, 0.429), rgba(0, 108, 138, 0.156), rgba(0, 136, 174, 0.296), rgba(0, 136, 174, 0.683), rgba(0, 136, 174, 0.822));

    --color-border-scroll-bar: black;

    --bg-scroll: rgb(0, 4, 73);

    --bg-body: black;


    --bg-menu: rgba(0, 0, 0, 0.844);

    --border-left-menu: gray;


    --color-header-bg: linear-gradient(180deg, rgb(8, 0, 53), rgba(0, 12, 77, 0.67), rgba(0, 3, 69, 0.316), rgba(7, 0, 75, 0.398));

    --color-text: white;

    --color-text-border: rgb(0, 0, 162);

    --color-text-border-hover: rgba(0, 0, 200, 0.718);

    --bg-next-prev: gray;

    --bg-next-prev-hover: white;

    --border-barseach: rgb(89, 89, 89);




    --bg-home-card: rgba(0, 0, 0, .5);

